<template>
    <div>
      科技成果预览
      <div style="width:500px;height:500px" ref="chart"></div>
    </div>
</template>

<script>
    export default {
        name: "Achievement",
        data(){
            return {
                option:{
                    title : {
                        text: '某站点用户访问来源',
                        subtext: '纯属虚构',
                        x:'center'
                    },
                    tooltip : {
                        trigger: 'item',
                        formatter: "{a} <br/>{b} : {c} ({d}%)"
                    },
                    legend: {
                        orient: 'vertical',
                        left: 'left',
                        // data:''
                        data: ''
                    },
                    series : [
                        {
                            name: '访问来源',
                            type: 'pie',
                            radius : '55%',
                            center: ['50%', '60%'],
                            data:'',
                            itemStyle: {
                                emphasis: {
                                    shadowBlur: 10,
                                    shadowOffsetX: 0,
                                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                                }
                            }
                        }
                    ]
                }
            }
        },
        methods:{
            //初始化e-charts
            initCharts () {
                let myChart = this.$echarts.init(this.$refs.chart);
                console.log(this.$refs.chart)
                // 绘制图表
                myChart.setOption(this.option);
            },
            //获取饼图数据
           getPieInfo(){
                var that = this;
                console.log('获取饼图数据')
                this.$axios.get('http://192.168.1.104:8088/demands/demandsIndustryProp')
                    .then((res)=>{
                        if (res.data.code==0){
                            var arr = [];
                            res.data.data.sdata.forEach((item,key)=>{
                                arr.push({value:item.value,name:item.name})
                            })
                            console.log(arr)
                            that.option.legend.data = res.data.data.ldata;
                            that.option.series[0].data = arr;
                            that.initCharts();
                        }
                        console.log(res)
                    })
           }
        },
        mounted() {
            this.getPieInfo();
        },
        created() {
        }
    }
</script>

<style scoped lang="less">

</style>



